.join {
  display: flex;
  padding: 0 2rem;
  gap: 10rem;
}
.left-j {
  color: #fff;
  font-size: 3rem;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
}
.left-j > hr {
  position: absolute;
  width: 12.5rem;
  border: 2px solid var(--firstColor);
  border-radius: 20%;
  margin: 1px 0;
}
.left-j > div {
  display: flex;
  gap: 1rem;
}
.right-j {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.email-container {
  display: flex;
  gap: 3rem;
  background-color: gray;
  padding: 1rem 2rem;
}
.email-container > input {
  background-color: transparent;
  border: none;
  outline: none;
  color: var(--lightgray);
  font-size: 1.4rem;
}
::placeholder {
  color: var(--lightgray);
  font-size: 1.4rem;
}
.btn-j {
  background-color: var(--firstColor);
  color: white;
}
@media screen and (max-width: 768px) {
  .join {
    flex-direction: column;
    gap: 6rem;
  }
  .left-j > hr {
    display: none;
  }
  .left-j > div {
    font-size: xx-large;
    text-align: center;
    display: flex;
    flex-direction: column;
  }
  .right-j {
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .email-container {
    display: flex;
    gap: 2rem;
    background-color: gray;
    padding: 0.5rem 1rem;
  }
  .email-container > input {
    background-color: transparent;
    border: none;
    outline: none;
    color: var(--lightgray);
    font-size: 1rem;
  }
  ::placeholder {
    font-size: 1rem;
  }
}
