.reasons {
  padding: 0 2rem;
  display: flex;
  grid-template: 2rem;
}
.left-r {
  position: relative;
  top: 1rem;
  flex: 1 1;
  display: grid;
  grid-template-columns: repeat(3, 0fr);
  gap: 1rem;
  grid-auto-rows: 1fr;
}
.left-r > img {
  object-fit: cover;
}
.left-r > :nth-child(1) {
  width: 12rem;
  grid-row: 1/3;
  height: 28rem;
}
.left-r > :nth-child(2) {
  width: auto;
  height: 16rem;
  grid-column: 2/4;
}
.left-r > :nth-child(3) {
  width: 14rem;
  height: 11rem;
  grid-column: 2/3;
  grid-row: 2;
}
.left-r > :nth-child(4) {
  width: 10rem;
  height: 11rem;
  grid-row: 2;
  grid-column: 3/4;
}
.right-r {
  position: relative;
  top: 1rem;
  position: relative;
  flex: 1 1;
  text-transform: uppercase;
  gap: 1rem;
  display: flex;
  flex-direction: column;
}
.right-r > span {
  text-transform: uppercase;
  color: var(--firstColor);
  font-weight: bold;
  letter-spacing: 1px;
}
.right-r > div {
  color: #fff;
  font-size: 3rem;
  font-weight: bold;
}
.details-r {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.details-r > div {
  display: flex;
  font-size: 1rem;
  gap: 1rem;
}
.details-r > div > img {
  width: 2rem;
  height: 2rem;
}
.partness {
  display: flex;
  gap: 1rem;
}
.partness > img {
  width: 2.5rem;
}
.reasonsHook {
  position: relative;
  top: 2rem;
}
@media screen and (max-width: 768px) {
  .reasonsHook {
    top: 18rem;
  }
  .reasons {
    flex-direction: column;
    margin-top: -2rem;
  }
  .left-r {
    grid-auto-rows: auto;
    overflow: hidden;
    gap: 1rem;
    justify-content: center;
  }
  .left-r > :nth-child(1) {
    width: 35vw;
    height: 85vw;
    max-height: 460px;
  }
  .left-r > :nth-child(2) {
    width: 43vw;
    height: 50vw;
    max-width: 245px;
    max-height: 250px;
  }
  .left-r > :nth-child(3) {
    width: 20vw;
    height: 30vw;
    max-width: 120px;
    max-height: 195px;
  }
  .left-r > :nth-child(4) {
    width: 18vw;
    height: 30vw;
    max-width: 110px;
    max-height: 195px;
  }
  .right-r {
    display: flex;
    margin-top: 2rem;
    align-items: center;
  }
  .right-r > div {
    color: #fff;
    font-size: xx-large;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  .right-r > :nth-child(1) {
    align-self: flex-start;
  }

  .partness {
    gap: 4rem;
  }
  .partness > img {
    width: 2.5rem;
  }
}
