.header {
  display: flex;
  justify-content: space-between;
}
.floating-button {
  position: fixed;
  z-index: 1;
  right: -1rem;
  top: 0.5rem;
  cursor: pointer;
  border: none;
  background: transparent;
}
.floating-button > img {
  transform: rotate(180deg);
  width: 8rem;
}
.header span {
  font-family: "Zen Dots", cursive;
  color: var(--lightgray);
  position: relative;
  border-bottom: 3px solid var(--lightgray);
  left: 1rem;
  top: 0.7rem;
  font-size: 1.3rem;
  text-transform: uppercase;
  height: 1.8rem;
  width: 14rem;
}
.header span::after {
  content: "";
  position: absolute;
  left: 0rem;
  top: 1.8rem;
  width: 180px;
  border-bottom: 3px solid var(--firstColor);
  animation: moveBar 5s linear infinite;
}
@keyframes moveBar {
  0% {
    width: 0rem;
  }
  50% {
    width: 14rem;
  }
  100% {
    width: 0rem;
  }
}
.header-menu {
  list-style: none;
  display: flex;
  gap: 2rem;
  color: white;
}

.header-menu > li:hover {
  color: var(--firstColor);
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .floating-button {
    display: none;
  }
  .header span {
    font-size: 1rem;
    width: 10.6rem;
  }
  @keyframes moveBar {
    0% {
      width: 0rem;
    }
    50% {
      width: 10.6rem;
    }
    100% {
      width: 0rem;
    }
  }
  .header > :nth-child(3) {
    position: fixed;
    right: 2rem;
    z-index: 99;
  }
  .header-menu {
    flex-direction: column;
    background-color: var(--menuColor);
    padding: 3rem;
    border-radius: 0.5rem;
  }
}
