.footerContainer {
  position: relative;
}
.footerContainer > hr {
  border: 1px solid var(--lightgray);
}
.footer {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  align-items: center;
  justify-content: center;
  height: 20rem;
}
.social {
  display: flex;
  gap: 4rem;
}
.social > img {
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
.logo-f span {
  font-family: "Zen Dots", cursive;
  color: var(--lightgray);
  position: relative;
  border-top: 3px solid var(--lightgray);
  font-size: 2rem;
  text-transform: uppercase;
  height: 1.8rem;
  width: 14rem;
}
.logo-f > span::before {
  content: "";
  position: absolute;
  left: 0rem;
  top: -0.15rem;
  border-bottom: 3px solid var(--firstColor);
  animation: moveBarFooter 5s linear infinite;
}
@keyframes moveBarFooter {
  0% {
    width: 0rem;
  }
  50% {
    width: 21.2rem;
  }
  100% {
    width: 0rem;
  }
}
.blurFooter {
  position: absolute;
  left: -5rem;
  top: -5rem;
  width: 35em;
  height: 25rem;
}
@media screen and (max-width: 768px) {
  .logo-f span {
    font-size: 1.7rem;
  }
}
