.Testimonials {
  display: flex;
  gap: 1rem;
  padding: 0 2rem;
}
.left-t {
  display: flex;
  flex: 1 1;
  gap: 2rem;
  flex-direction: column;
  text-transform: uppercase;
  color: #fff;
}
.left-t > div {
  display: flex;
  flex-direction: column;
}
.left-t div > :nth-child(1) {
  color: var(--firstColor);
  font-weight: bold;
  letter-spacing: 1px;
}
.left-t div > :nth-child(2),
.left-t div > :nth-child(3) {
  font-size: bold;
  font-size: 3rem;
}
.left-t > :nth-child(2) {
  text-transform: none;
  text-align: justify;
  letter-spacing: 2px;
  line-height: 40px;
}
.right-t {
  flex: 1;
  position: relative;
}
.right-t > img {
  position: absolute;
  width: 23rem;
  height: 23rem;
  object-fit: cover;
  right: 8rem;
  top: 1.5rem;
  border-radius: 50%;
}
.right-t > :nth-child(1) {
  z-index: 1;
  position: absolute;
  width: 23rem;
  height: 23rem;
  background-color: transparent;
  right: 7.8rem;
  top: 1.5rem;
  border-left: 0.5rem solid var(--firstColor);
  border-radius: 50%;
  animation: borderMove linear 1s infinite;
}
@keyframes borderMove {
  to {
    border-left: 0.5rem solid var(--firstColor);
    transform: rotate(360deg);
  }
  0% {
    border-color: var(--firstColor);
  }
  25% {
    border-color: var(--cyan);
  }
  100% {
    border-color: var(--firstColor);
  }
}
.right-t > :nth-child(2) {
  position: absolute;
  width: 20rem;
  height: 20rem;
  top: 0rem;
  right: 6rem;
  background: var(--planCard);
}
.arrows {
  display: flex;
  gap: 1rem;
  position: absolute;
  bottom: 1rem;
  left: 3rem;
}
.arrows > img {
  width: 1.5rem;
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .Testimonials {
    margin-top: -3rem;
    position: relative;
    flex-direction: column;
  }
  .left-t div > :nth-child(1) {
    font-size: 0.7rem;
  }
  .left-t div > :nth-child(2) {
    padding-top: 0.5rem;
  }
  .left-t div > :nth-child(2),
  .left-t div > :nth-child(3) {
    font-size: bold;
    font-size: 1.5rem;
  }
  .left-t > :nth-child(2) {
    margin-top: -1.5rem;
    text-transform: none;
    letter-spacing: 0px;
    line-height: 18px;
    font-size: smaller;
    height: 35vh;
  }
  .right-t {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }
  .right-t > img {
    position: relative;
    max-width: 90vw;
    height: auto;
    top: 0rem;
    left: 0rem;
    right: 0rem;
    border-radius: 0;
    object-fit: cover;
  }
  .right-t > :nth-child(1) {
    display: none;
  }
  .right-t > :nth-child(2) {
    display: none;
  }
  .arrows {
    padding-top: 1rem;
    position: relative;
    left: 0;
  }
  .status {
    font-size: 0.75rem;
    text-align: center;
  }
}
