.programs {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 0 2rem;
  height: 30rem;
}
.programsHeader {
  display: flex;
  gap: 5rem;
  font-weight: bold;
  font-size: 3rem;
  justify-content: center;
  color: #fff;
  text-transform: uppercase;
  font-style: italic;
}
.programCategories {
  display: flex;
  margin-top: 4rem;
  gap: 1rem;
}
.category {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: var(--gray);
  padding: 1rem;
  gap: 1rem;
  color: #fff;
  justify-content: space-between;
  width: 33%;
}
.category > :nth-child(1) {
  width: 2.2rem;
  height: 2.2rem;
  fill: #fff;
}
.category > :nth-child(2) {
  font-size: 1rem;
  font-weight: bold;
}
.category > :nth-child(3) {
  font-size: 0.9rem;
  line-height: 25px;
}
.joinNow {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.joinNow > img {
  width: 1rem;
}
.category:hover {
  background: var(--planCard);
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .programs {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .programsHeader {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 0.4rem;
    font-size: 7vw;
    margin-top: 4rem;
  }
  .programCategories {
    margin-top: -1.2rem;
    flex-direction: column;
    width: 80vw;
    max-width: 450px;
  }
  .category {
    width: fit-content;
    height: 20rem;
    background: var(--planCard);
  }
}
