.plansHeader {
  display: flex;
  gap: 2rem;
  font-weight: bold;
  font-size: 3rem;
  justify-content: center;
  color: #fff;
  text-transform: uppercase;
  font-style: italic;
}
.plansContainer {
  margin-top: -4rem;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  position: relative;
}
.plans {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
}
.plan {
  display: flex;
  flex-direction: column;
  background-color: var(--caloryCard);
  color: #fff;
  gap: 2rem;
  padding: 1.5rem;
  width: 18rem;
  font-weight: bold;
}
.plan:nth-child(2) {
  background: var(--planCard);
  transform: scale(1.1);
}
.plan > svg {
  fill: var(--firstColor);
  width: 2rem;
  height: 2rem;
}
.plan > :nth-child(2) {
  font-size: 1rem;
  font-weight: bold;
}
.plan > :nth-child(3) {
  font-size: 3rem;
  font-weight: bold;
}
.plan > :nth-child(5) {
  font-size: 0.8rem;
}
.features {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.feature {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.feature > img {
  width: 1rem;
  color: #fff;
}
.plans button {
  font-weight: bold;
}
.plans > :nth-child(2) > svg {
  fill: #fff;
}
.plans > :nth-child(2) > button {
  font-weight: bold;
  color: var(--firstColor);
}
.plansBlurRight {
  position: absolute;
  top: 6rem;
  left: 0;
  width: 23rem;
  height: 30rem;
}
.plansBlurLeft {
  position: absolute;
  right: 0;
  top: 6rem;
  width: 23rem;
  height: 30rem;
}
@media screen and (max-width: 768px) {
  .plansHeader {
    gap: 0.5rem;
    text-align: center;
    flex-direction: column;
  }
  .plansHeader > span {
    font-size: xx-large;
  }
  .plans {
    flex-direction: column;
  }
  .plans > :nth-child(2) {
    transform: none;
  }
  .plan {
    width: 16rem;
    padding: 1rem;
  }
}
