.hero {
  display: flex;
  justify-content: space-between;
}
.left-h {
  padding: 2rem;
  padding-top: 1.5rem;
  flex: 3;
  display: flex;
  gap: 2rem;
  flex-direction: column;
}
.right-h {
  flex: 1;
  height: 100vh;
  background-color: var(--firstColor);
  position: relative;
  border-radius: 20rem 0px 0px 0px;
}

.the-best-ad {
  margin-top: 4rem;
  background-color: #363d429f;
  border-radius: 4rem;
  width: fit-content;
  padding: 20px 13px;
  text-transform: uppercase;
  color: #fff;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.the-best-ad > span {
  z-index: 2;
  letter-spacing: 1px;
}
.the-best-ad > div {
  position: absolute;
  background-color: var(--firstColor);
  width: 6rem;
  height: 80%;
  left: 8px;
  border-radius: 3rem;
  z-index: 1;
  animation: move 4s linear infinite;
}
@keyframes move {
  0% {
    left: 0;
  }
  50% {
    left: calc(100% - 6rem);
  }
  100% {
    left: 0;
  }
}
.hero-text {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  font-size: 4.5rem;
  font-weight: bold;
  color: #fff;
  text-overflow: inherit;
}
.hero-text > div:nth-of-type(3) {
  font-size: 1rem;
  font-weight: 200;
  text-transform: none;
  letter-spacing: 1.2px;
  width: 90%;
}

.figures {
  display: flex;
  gap: 3rem;
}
.figures > div {
  display: flex;
  flex-direction: column;
}
.fontSize {
  font-size: 1rem;
}
.figures > div > span:nth-of-type(1) {
  color: #fff;
  font-size: 2rem;
}
.figures > div > span:nth-of-type(2) {
  color: var(--gray);
  font-size: 1rem;
  text-transform: uppercase;
}
.btn {
  background-color: #fff;
  padding: 0.3rem;
  font-size: 1.2rem;
  font-weight: 400;
  border: 4px solid transparent;
  border-radius: 2px;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn:hover {
  cursor: pointer;
}
.hero-buttons {
  display: flex;
  gap: 1.5rem;
  font-weight: normal;
}
.hero-buttons > :nth-child(1) {
  color: #fff;
  background-color: var(--firstColor);
  width: 7rem;
}
.hero-buttons > :nth-child(1):hover {
  box-shadow: -4px 4px 4px rgba(0, 0, 0, 0.5);
}
.hero-buttons > :nth-child(2) {
  color: #fff;
  background-color: transparent;
  width: 7rem;
  border: 2px solid var(--firstColor);
}
.hero-buttons > :nth-child(2):hover {
  box-shadow: -4px 4px 4px rgba(0, 0, 0, 0.5);
}
#joinBtn {
  position: absolute;
  right: 3rem;
  top: 2rem;
  padding: 0.5rem;
  color: black;
  border-radius: 2px;
}
#joinBtn:hover {
  background-color: var(--lightgray);
}
.heart-rate {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  background-color: var(--darkGrey);
  width: fit-content;
  height: fit-content;
  padding: 0.5rem;
  border-radius: 5px 5px 5px 5px;
  position: absolute;
  right: 2rem;
  top: 8rem;
}
.heart-rate > img {
  position: absolute;
  left: 0.5rem;
  width: 1.5rem;
  animation: pulse-heart 1.2s ease-in-out infinite;
}
@keyframes pulse-heart {
  0% {
    transform: scale(1);
  }
  40% {
    transform: scale(1.3);
  }
  60% {
    transform: scale(1.2);
  }
  80% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}
.heart-rate > :nth-of-type(1) {
  color: var(--gray);
  position: relative;
  top: 0.5rem;
}
.heart-rate > :nth-of-type(2) {
  color: #fff;
  font-size: 1.5rem;
  position: relative;
}
.hero-image {
  position: absolute;
  z-index: 1;
  top: 8.32rem;
  right: 8.5rem;
  width: 23rem;
}
.hero-image-back {
  position: absolute;
  top: 3rem;
  right: 20rem;
  z-index: -1;
  width: 15rem;
}
.calories {
  display: flex;
  gap: 2rem;
  background-color: var(--caloryCard);
  border-radius: 5px;
  padding: 1rem;
  width: fit-content;
  position: absolute;
  top: 33rem;
  right: 26rem;
}
.calories > img {
  width: 3rem;
}
.calories > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.calories > div > :nth-child(1) {
  color: var(--gray);
}
.calories > div > :nth-child(2) {
  color: #fff;
  font-size: 1.6rem;
}
.programHook {
  position: absolute;
  top: 4rem;
}
.heroBlur {
  width: 22rem;
  height: 30rem;
  top: -5rem;
  left: 4rem;
}
@media screen and (max-width: 768px) {
  .hero {
    flex-direction: column;
  }
  .heroBlur {
    width: 10rem;
  }
  .the-best-ad {
    text-align: center;
    height: 1rem;
    font-size: small;
    align-self: center;
  }
  .hero-text {
    font-size: 1.8rem;
    align-items: center;
    justify-content: center;
  }
  .hero-text > div:nth-of-type(3) {
    font-size: 0.7rem;
    font-weight: 200;
    margin-top: 1.5rem;
    letter-spacing: 1px;
    text-align: center;
  }
  .hero-buttons {
    justify-content: center;
  }
  .figures {
    text-align: center;
    justify-content: center;
    gap: 2rem;
  }
  .figures > div > span:nth-of-type(1) {
    font-size: large;
  }
  .figures > div > span:nth-of-type(2) {
    font-size: 0.6rem;
  }
  .right-h {
    display: flex;
    position: relative;
    background: none;
    justify-content: center;
    align-items: center;
    justify-items: center;
    width: 100%;
  }
  .heart-rate {
    background-color: #636161;
    width: 8rem;
    left: 10vw;
    top: 0rem;
  }
  .heart-rate > img {
    width: 1.2rem;
  }
  .heart-rate > :nth-of-type(1) {
    font-size: 0.8rem;
  }
  .heart-rate > :nth-of-type(2) {
    font-size: 1.2rem;
  }
  #joinBtn {
    right: 10vw;
  }
  .calories {
    gap: 0.5rem;
    position: relative;
    top: 2rem;
    left: 10vw;
    padding: 0.5rem 0.3rem;
  }
  .calories > img {
    width: 1.8rem;
  }
  .calories > div > :nth-child(1) {
    font-size: 0.9rem;
  }
  .calories > div > :nth-child(2) {
    color: white;
    font-size: 0.9rem;
  }
  .hero-image {
    position: relative;
    width: 14rem;
    left: 4rem;
    top: 6rem;
    align-self: center;
  }
  .hero-image-back {
    width: 12rem;
    align-self: center;
    top: -2rem;
    right: 40vw;
  }
  .programHook {
    position: relative;
    width: 10rem;
    top: 35rem;
  }
}
